$sz_5: 2rem; //32px
$sz_6: 2.5rem;
$sz_6b: 2.75rem;
$sz_7: 3rem; //48px
$sz_8: 3.5rem;
$sz_9: 4rem; //64px
$sz_10: 5rem; //80px
$sz_11: 6rem; //96px
$sz_12: 7rem;
$sz_13: 8rem; //128px
$sz_14: 9rem;
$sz_15: 10rem; //160px
$sz_20: 20rem; //320px
$sz_30: 30rem; //480px
$sz_40: 40rem; //640px
$sz_50: 50rem; //800px
