// Shards variables
$variable-prefix: bs-; // Deprecated in v5.2.0 for the shorter `$prefix`
$prefix: $variable-prefix;
$debug: true;

$font-path: '/fonts/';
$icon-prefix: hui;
$icon-size: 24px;
$icon-size-scale-2xs: 10;
$icon-size-scale-xs: 12;
$icon-size-scale-sm: 14;
$icon-size-scale-base: 16;
$icon-size-scale-lg: 20;
$icon-size-scale-xl: 24;
$icon-size-scale-2xl: 32;

$icon-sizes: (
  '2xs': $icon-size-scale-2xs,
  'xs': $icon-size-scale-xs,
  'sm': $icon-size-scale-sm,
  'lg': $icon-size-scale-lg,
  'xl': $icon-size-scale-xl,
  '2xl': $icon-size-scale-2xl,
);

// $superclass: "mojo";
$superclass: false;

// scss-docs-start theme-text-variables
$primary-text-emphasis: shade-color($primary, 60%);
$secondary-text-emphasis: shade-color($secondary, 60%);
$success-text-emphasis: shade-color($success, 60%);
$info-text-emphasis: shade-color($info, 60%);
$warning-text-emphasis: shade-color($warning, 60%);
$danger-text-emphasis: shade-color($danger, 60%);
$light-text-emphasis: $gray-700;
$dark-text-emphasis: $gray-700;
// scss-docs-end theme-text-variables

// scss-docs-start theme-bg-subtle-variables
$primary-bg-subtle: tint-color($primary, 80%);
$secondary-bg-subtle: tint-color($secondary, 80%);
$success-bg-subtle: tint-color($success, 80%);
$info-bg-subtle: tint-color($info, 80%);
$warning-bg-subtle: tint-color($warning, 80%);
$danger-bg-subtle: tint-color($danger, 80%);
$light-bg-subtle: mix($gray-100, $white);
$dark-bg-subtle: $gray-400;
// scss-docs-end theme-bg-subtle-variables

// scss-docs-start theme-border-subtle-variables
$primary-border-subtle: tint-color($primary, 60%);
$secondary-border-subtle: tint-color($secondary, 60%);
$success-border-subtle: tint-color($success, 60%);
$info-border-subtle: tint-color($info, 60%);
$warning-border-subtle: tint-color($warning, 60%);
$danger-border-subtle: tint-color($danger, 60%);
$light-border-subtle: $gray-200;
$dark-border-subtle: $gray-500;
// scss-docs-end theme-border-subtle-variables

$min-contrast-ratio: 4.5;

// Customize the light and dark text colors for use in our color contrast function.
$color-contrast-dark: $dark;
$color-contrast-light: $light;

// Set a specific jump point for requesting color jumps
$theme-color-interval: 8%;

// The yiq lightness value that determines when the lightness of color changes from "dark" to "light". Acceptable values are between 0 and 255.
$yiq-contrasted-threshold: 150;

// Customize the light and dark text colors for use in our YIQ color contrast function.
$yiq-text-dark: $gray-900;
$yiq-text-light: $white;

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-rounded: true;
$enable-shadows: true;
$enable-gradients: false; // Not supported by Shards.
$enable-transitions: true;
$enable-hover-media-query: false; // Deprecated, no longer affects any compiled CSS
$enable-fonts-import: true;
$enable-caret: true;

$enable-reduced-motion: true;
$enable-smooth-scroll: true;
$enable-grid-classes: true;
$enable-container-classes: true;
$enable-cssgrid: false;
$enable-button-pointers: true;
$enable-rfs: true;
$enable-validation-icons: true;
$enable-negative-margins: false;
$enable-deprecation-messages: true;
$enable-important-utilities: true;
$enable-display-sizes: true;

$enable-dark-mode: false; // dark is the new default
$color-mode-type: data; // `data` or `media-query`

// TODO: add $enable-light-mode: true; functionality

// Forms

$border-color: #becad6;
$input-frozen-color: #abb6bf;

// Spacing

$spacer: 1.5rem; // ~ 24px

// Body

$body-bg: $body;
$body-color: $text;

// Links
$link-decoration: none;
$link-hover-decoration: none;

// Grid breakpoints

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

@include _assert-ascending($grid-breakpoints, '$grid-breakpoints');
@include _assert-starts-at-zero($grid-breakpoints);

// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
  xxl: 1320px,
);

@include _assert-ascending($container-max-widths, '$container-max-widths');

$grid-columns: 12;
$grid-gutter-width: 1.5rem;
$grid-row-columns: 6;
$container-padding-x: $grid-gutter-width;
$break-point-limit: 'medium';
$tile-spacing: 0.75rem;

// Components

$line-height-lg: 1.5;
$line-height-sm: 1.5;

$border-width: 0.0625rem;
$border-width-lg: 0.188rem;
$border-width-sm: 0.031rem;

$border-radius: 0.375rem;
$border-radius-lg: 0.5rem;
$border-radius-sm: 0.35rem;
$border-radius-xl: 1rem;
$border-radius-2xl: 2rem;
$border-radius-pill: 50rem;

$border-style: solid;
$border-color: $gray-700;
$border-color-translucent: rgba($gray-700, 0.175);
$enable-angled-edges: true;
$angle-edge-size: 3;

$hr-margin-y: $spacer;
$hr-color: inherit;
$hr-border-color: null; // Allows for inherited colors
$hr-border-width: $border-width;
$hr-opacity: 0.25;

$box-shadow: 0 0.5rem 1rem rgba($dark, 0.15);
$box-shadow-sm: 0 0.125rem 0.25rem rgba($dark, 0.075);
$box-shadow-lg: 0 1rem 3rem rgba($dark, 0.175);
$box-shadow-inset: inset 0 1px 2px rgba($dark, 0.075);

$component-active-color: $white;
$component-active-bg: theme-color('primary');

// Easings
$ease-in-out-circ: cubic-bezier(0.27, 0.01, 0.38, 1.06);

// Transitions
$transition-duration: 250ms;
$transition-base: all $transition-duration $ease-in-out-circ;
$transition-fade: opacity 200ms ease-in-out;
$transition-collapse: height 350ms ease-in-out;
$transition-move: 420ms $ease-in-out-circ;

// Harmony
$modular-ratio: $MAJOR_THIRD;
$line-height-base: 1.5;
$block-spacing: 1.5rem;
$vertical-unit: 0.5rem;
$main-unit: rem;
$main-indent: 0.24rem;
$spacers: (
  0: 0,
  1: $spacer * 0.25,
  2: $spacer * 0.5,
  3: $spacer,
  4: $spacer * 1.5,
  5: $spacer * 3,
);
$gutters: $spacers;

$font-sizes: (
  xs: 0.875rem,
  // 14px
  sm: 1rem,
  // 16px
  md: 1.25rem,
  // 30px
  lg: 1.5rem,
  // 24px
  xl: 2rem,
  // 32px
);

$font-line-sizes: (
  xs: map.get($font-sizes, 'xs') * $line-height-base,
  sm: map.get($font-sizes, 'sm') * $line-height-base,
  md: map.get($font-sizes, 'md') * $line-height-base,
  lg: map.get($font-sizes, 'lg') * $line-height-base,
  xl: map.get($font-sizes, 'xl') * $line-height-base,
);

//
// Title, Subtitle, Display Font Variables
//

$title-size: 3.5rem;
$title-weight: 700;
$title-strong-weight: inherit;

$subtitle-size: 2rem;
$subtitle-weight: 400;
$subtitle-strong-weight: 500;

$display-font-family: null;
$display-font-style: null;
$display-font-weight: 300;
$display-line-height: 1.2;

$display-font-sizes: if(
  $enable-display-sizes,
  (
    1: 5rem,
    2: 4.5rem,
    3: 4rem,
    4: 3.5rem,
    5: 3rem,
    6: 2.5rem,
  ),
  null
);

// Optionally Load Fonts this way
// Default theme will turn this off in the theme file unless you remove it
// which will then fallback to this.
$google-fonts-import: 'https://fonts.googleapis.com/css?family=Poppins:300,400,500,600|Roboto+Mono'; // Includes: Poppins: Light, Regular, Medium, Semi-bold; Roboto Mono: Regular

$font-family-poppins-first: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial,
  sans-serif;
$font-family-system-first: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
$font-family-roboto-mono-first: 'Roboto Mono', Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;

$font-family-sans-serif: $font-family-system-first;
$font-family-monospace: $font-family-roboto-mono-first;
$font-family-base: $font-family-sans-serif;

// $font-family-base: "Jost";
// $font-family-code: "Space Mono";

// Typography Variables
// ============================================================================
// TODO: sort out the heading variables!!!
$font-weight-base: $font-weight-normal;

$font-size-root: strip-units(map.get($font-sizes, 'sm'));
$font-size-base: map.get($font-sizes, 'sm');
$font-size-lg: map.get($font-sizes, 'lg');
$font-size-sm: map.get($font-sizes, 'xs');

$font-weight-lighter: lighter;
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-medium: 500;
$font-weight-semibold: 600;
$font-weight-bold: 700;
$font-weight-bolder: bolder;

$font-weight-base: $font-weight-normal;

$h1-font-size: $font-size-base * 2.5;
$h2-font-size: $font-size-base * 2;
$h3-font-size: $font-size-base * 1.75;
$h4-font-size: $font-size-base * 1.5;
$h5-font-size: $font-size-base * 1.25;
$h6-font-size: $font-size-base;

$font-sizes: (
  1: $h1-font-size,
  2: $h2-font-size,
  3: $h3-font-size,
  4: $h4-font-size,
  5: $h5-font-size,
  6: $h6-font-size,
);

$font-weight-normal: 400;
$font-weight-bold: 700;

$font-weight-base: $font-weight-normal;

$h1-font-size: 3.052rem;
$h2-font-size: 2.441rem;
$h3-font-size: 1.953rem;
$h4-font-size: 1.563rem;
$h5-font-size: 1.25rem;
$h6-font-size: 1rem;

$headings-margin-bottom: 0.75rem;

$headings-font-family: $font-family-poppins-first;
$headings-font-weight: 400;
$headings-color: $light;
$headings-letter-spacing: -0.0625rem; // ~1px

$h1-line-height: 3rem;
$h2-line-height: 2.25rem;
$h3-line-height: 2.25rem;
$h4-line-height: 2rem;
$h5-line-height: 1.5rem;
$h6-line-height: 1.5rem;

$display1-size: 7.451rem;
$display2-size: 5.96rem;
$display3-size: 4.768rem;
$display4-size: 3.815rem;

$display-line-height: 1;
$display-margin-bottom: 0.75rem;

$paragraphs-margin-bottom: 1.75rem;

$lead-line-height: 1.875rem;

$small-font-size: 80%;

$text-muted: $gray-600;

$blockquote-font-size: 1.5rem;
$blockquote-footer-font-size: 1.125rem;
$blockquote-margin-bottom: 0.75rem;

$hr-border-color: rgba($dark, 0.1);
$hr-border-width: $border-width;
$hr-margin-y: 1.125rem;

$mark-padding: 0.2em;
$mark-bg: #fff09e;

// Accordions
$accordion-max-height: 20rem;
$transition-color: #3f3f3f;

// Tables
//
// Customizes the `.table` component with basic values, each used across all table variations.
$table-color: $light;
$table-bg: $black;
$table-active-bg: rgba($primary, 0.075);
$table-striped-bg: rgba($dark, 0.075);
$table-hover-bg: rgba($dark, 0.075);
$table-cell-padding: 0.75rem;
$table-cell-padding-sm: 0.3rem;

$table-dark-color: $body-bg;
$table-dark-bg: $gray-900;
$table-dark-border-color: lighten($gray-900, 7.5%);
$table-dark-accent-bg: rgba($light, 0.05);
$table-dark-hover-bg: rgba($light, 0.075);

$table-head-color: $gray-700;
$table-head-bg: $gray-200;
$table-border-color: $gray-300;

// Buttons
//
// For each of Bootstrap's buttons, define text, background and border color.

$btn-padding-y-lg: 0.75rem;
$btn-padding-x-lg: 1.75rem;

$btn-line-height-sm: $line-height-sm;
$btn-line-height-lg: $line-height-lg;

$btn-padding-y-sm: 0.35rem;
$btn-padding-x-sm: 1rem;

$input-btn-padding-y: 0.5rem;
$input-btn-padding-x: 1rem;
$input-btn-line-height: 1.125;

$input-btn-padding-y-sm: 0.35rem;
$input-btn-padding-x-sm: 0.75rem;

$input-btn-padding-y-lg: 0.75rem;
$input-btn-padding-x-lg: 1rem;

$btn-pill-border-radius: 50px;

$btn-padding-y: 0.75rem;
$btn-padding-x: 1.25rem;

$btn-font-weight: $font-weight-normal;
$btn-font-size: 0.875rem;
$btn-font-size-sm: 0.75rem;
$btn-font-size-lg: 1.125rem;
$btn-font-family: $font-family-poppins-first;
$btn-box-shadow: none;

$input-btn-focus-width: 0.2rem;

$btn-active-box-shadow: inset 0 3px 5px rgba($black, 0.125);

$btn-link-disabled-color: $gray-600;
$btn-block-spacing-y: 0.5rem;

// Allows for customizing button radius independently from global border radius
$btn-border-radius: $border-radius;
$btn-border-radius-lg: $border-radius-lg;
$btn-border-radius-sm: $border-radius-sm;

$btn-border-width: 1px;

// $btn-transition: $transition-base;
$btn-transition: 'all 180ms linear';
// Forms

$input-padding-y: $input-btn-padding-y;
$input-padding-x: $input-btn-padding-x;
$input-line-height: 1.5;

$input-padding-y-sm: $input-btn-padding-y-sm;
$input-padding-x-sm: $input-btn-padding-x-sm;
$input-line-height-sm: 1.5;

$input-padding-y-lg: $input-btn-padding-y-lg;
$input-padding-x-lg: $input-btn-padding-x-lg;
$input-line-height-lg: 1.5;

$input-bg: $white;
$input-disabled-bg: #f5f6f7;
$input-border-radius: $border-radius;
$input-border-radius-sm: $border-radius-sm;
$input-border-radius-lg: $border-radius-lg;

$input-font-size: 0.95rem;

$input-color: $gray-700;
$input-border-color: $border-color;
$input-btn-border-width: $border-width;
$input-box-shadow: none;

$input-border-width: $border-width;
$input-border-radius: $border-radius;
$input-border-radius-lg: $border-radius-lg;
$input-border-radius-sm: $border-radius-sm;

$input-focus-bg: $input-bg;
$input-focus-border-color: theme-color('primary');
$input-focus-box-shadow: 0 0.313rem 0.719rem rgba(theme-color('primary'), 0.1), 0 0.156rem 0.125rem rgba($black, 0.06);
$input-focus-color: $input-color;

$input-hover-border-color: darken(#becad6, 15);

$input-placeholder-color: $gray-600;

$input-height-border: $input-border-width * 2;

$input-height-inner: calc($input-font-size * $input-line-height) + calc($input-padding-y * 2);
$input-height: calc(#{$input-height-inner} + #{$input-height-border});

$input-height-inner-sm: calc($font-size-sm * $input-line-height-sm) + calc($input-padding-y-sm * 2);
$input-height-sm: calc(#{$input-height-inner-sm} + #{$input-height-border});

$input-height-inner-lg: calc($font-size-lg * $input-line-height-lg) + calc($input-padding-y-lg * 2);
$input-height-lg: calc(#{$input-height-inner-lg} + #{$input-height-border});

$input-transition: box-shadow $transition-duration $ease-in-out-circ, border $transition-duration $ease-in-out-circ;

$form-text-margin-top: 0.25rem;

$form-check-input-gutter: 1.25rem;
$form-check-input-margin-y: 0.313rem;
$form-check-input-margin-x: 0.313rem;

$form-check-inline-margin-x: 0.75rem;
$form-check-inline-input-margin-x: 0.3125rem;

$form-group-margin-bottom: 1rem;

$input-group-padding-y: 12px;
$input-group-padding-x: 14px;
$input-group-addon-bg: lighten($input-border-color, 19%);
$input-group-addon-border-color: $input-border-color;
$input-group-addon-color: $input-frozen-color;

$custom-control-gutter: 1.688rem;
$custom-control-spacer-x: 1rem;

$custom-control-indicator-size: 1.125rem;
$custom-control-indicator-bg: $white;
$custom-control-indicator-bg-size: 50% 50%;
$custom-control-indicator-box-shadow: none;
$custom-control-indicator-border: 1px solid $border-color;
$custom-checkbox-indicator-transition: transform $transition-duration $ease-in-out-circ,
  border $transition-duration $ease-in-out-circ;

$custom-control-indicator-disabled-bg: $gray-200;
$custom-control-description-disabled-color: $gray-600;

$custom-control-indicator-checked-color: $white;
$custom-control-indicator-checked-bg: theme-color('primary');
$custom-control-indicator-checked-box-shadow: none;

$custom-control-indicator-focus-box-shadow: $input-focus-box-shadow;

$custom-control-indicator-active-color: $white;
$custom-control-indicator-active-bg: lighten(theme-color('primary'), 35%);
$custom-control-indicator-active-box-shadow: none;

// Custom checkbox
$custom-checkbox-indicator-border-radius: 2px;
$custom-checkbox-indicator-icon-checked: none;
$custom-checkbox-indicator-indeterminate-bg: theme-color('primary');
$custom-checkbox-indicator-indeterminate-color: $custom-control-indicator-checked-color;
$custom-checkbox-indicator-icon-indeterminate: none;
$custom-checkbox-indicator-indeterminate-box-shadow: none;

// Custom radio
$custom-radio-indicator-border-radius: 50%;

// Custom select
$custom-select-padding-y: 0.375rem;
$custom-select-padding-x: 0.75rem;
$custom-select-height: $input-height; // OK
$custom-select-indicator-padding: 1rem; // Extra padding to account for the presence of the background-image based indicator
$custom-select-line-height: 1.2;
$custom-select-color: $input-color;
$custom-select-disabled-color: $gray-600;
$custom-select-bg: $white;
$custom-select-disabled-bg: $gray-200;
$custom-select-bg-size: 8px 10px; // In pixels because image dimensions
$custom-select-indicator-color: #333;
$custom-select-indicator: str-replace(
  url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'%3E%3Cpath fill='#{$custom-select-indicator-color}' d='M2 0L0 2h4zm0 5L0 3h4z'/%3E%3C/svg%3E"),
  '#',
  '%23'
);
$custom-select-border-width: $input-btn-border-width;
$custom-select-border-color: $input-border-color;
$custom-select-border-radius: $border-radius;

$custom-select-focus-border-color: $input-focus-border-color;
$custom-select-focus-box-shadow: 0 0.313rem 0.719rem rgba(theme-color('primary'), 0.1),
  0 0.156rem 0.125rem rgba($black, 0.06);

$custom-select-font-size-sm: 0.75rem;
$custom-select-height-sm: $input-height-sm;

$custom-select-height-lg: $input-height-lg;
$custom-select-font-size-lg: 1.25rem;

$custom-file-height: calc(2.428rem + 2px);
$custom-file-width: 14rem;
$custom-file-focus-box-shadow: $input-focus-box-shadow;
$custom-file-focus-border-color: $input-focus-border-color;

$custom-file-padding-y: 1rem;
$custom-file-padding-x: 0.5rem;
$custom-file-line-height: 1.5;
$custom-file-color: $gray-700;
$custom-file-bg: $white;
$custom-file-border-width: $border-width;
$custom-file-border-color: $input-border-color;
$custom-file-border-radius: $border-radius;
$custom-file-box-shadow: none;
$custom-file-button-color: $custom-file-color;
$custom-file-button-bg: $gray-200;
