);

// Custom switches (core)
$custom-switch-padding-left: 2.25rem;

// Form validation
$form-feedback-margin-top: $form-text-margin-top;
$form-feedback-font-size: $small-font-size;
$form-feedback-valid-color: theme-color('success');
$form-feedback-invalid-color: theme-color('danger');

// Custom toggle
$custom-toggle-width: 3.125rem;
$custom-toggle-height: 1.75rem;
$custom-toggle-border-width: 0.0625rem;
