$custom-toggle-checked-background: theme-color('success');
$custom-toggle-checked-border-color: $custom-toggle-checked-background;
$custom-toggle-checked-knob-background: $white;

$custom-toggle-knob-width: 1.25rem;
$custom-toggle-knob-height: 1.25rem;
$custom-toggle-knob-border-radius: 6.25rem;
$custom-toggle-knob-active-width: 1.625rem;

