$custom-toggle-width-sm: 2.1875rem;
$custom-toggle-height-sm: 1.125rem;

$custom-toggle-knob-width-sm: 0.75rem;
$custom-toggle-knob-active-width-sm: 1rem;
$custom-toggle-knob-height-sm: 0.75rem;

// Form validation
$form-feedback-valid-color: theme-color('success');
$form-feedback-invalid-color: theme-color('danger');

// Dropdowns
//
// Dropdown menu container and contents.

$dropdown-bg: $gray-600;
$dropdown-min-width: 10rem;
$dropdown-padding-y: 0.5rem;
$dropdown-spacer: 0;
$dropdown-border-color: rgba($black, 0.05);
$dropdown-border-width: $border-width;
$dropdown-divider-bg: $gray-200;
$dropdown-border-radius: $border-radius;

$dropdown-box-shadow: 0 0.5rem 4rem rgba(0, 0, 0, 0.11), 0 10px 20px rgba(0, 0, 0, 0.05), 0 2px 3px rgba(0, 0, 0, 0.06);

$dropdown-link-color: map.get($mojo-blue, hex);
$dropdown-link-hover-color: darken($gray-900, 5%);
$dropdown-link-hover-bg: lighten($dark, 55%);
$dropdown-link-active-color: $component-active-color;
$dropdown-link-active-bg: lighten($dark, 40%);
